import React from 'react'
import './Cards.css';

function CardItem(props) {
    return (
        <>
            <li className="cards-items">
                <a className="cards-item-link" href={props.pathname} target="_blank" rel="noopener noreferrer">
                    <figure className="cards-item-img-wrap" /*data-category={props.label}*/>
                        <img src={props.src} alt="App Image"
                            className="cards-item-img" />
                    </figure>
                    <div className="cards-item-info">
                        <h5 className="cards-item-text">{props.text}</h5>
                    </div>
                </a>
            </li>
        </>
    )
}

export default CardItem



