import React from 'react';
import './App.css';
import Navbar from './components/Navbar';
import { BrowserRouter as Router, Switch, Route } from 'react-router-dom';
import Home from './components/pages/HomePageData/Home';
import About from './components/pages/AboutPageData/About'
import Contact from './components/pages/ContactPageData/Contact';
import Resume from './components/pages/ResumePageData/Resume';
import Portfolio from './components/pages/PortfolioPageData/Portfolio'


function App() {
  return (
    <div className="App">
      <Router>
        <Navbar />
        <Switch>
          <Route path='/' exact component={Home} />
          <Route path='/about' exact component={About} />
          <Route path='/contact' exact component={Contact} />
          <Route path='/resume' exact component={Resume} />
          <Route path='/portfolio' exact component={Portfolio} />
        </Switch>

      </Router>

    </div>
  );
}

export default App;
