import React from 'react'
import { Link } from 'react-router-dom'
import { BsFileEarmarkCode } from 'react-icons/bs'
import './Footer.css'

function Footer() {
    return (
        <div className="footer-container">
            <div className="footer-links">
                <div className="footer-link-wrapper">
                    <div className="footer-link-items">
                        <h2>Contact</h2>
                        <p>Phone: +1 (403) 630 - 6292</p>
                        <p>Email: anthonyokwananke@gmail.com</p>
                        <Link to='./Contact'></Link>
                    </div>
                    <div className="footer-link-items">
                        <h2>Social</h2>
                        <a href="https://www.linkedin.com/in/anthony-okwananke"
                            rel="no opener noreferrer"
                            target="_blank">
                            LinkedIn
                         </a>
                        <a href="https://github.com/AnthonyOkwananke"
                            rel="no opener noreferrer"
                            target="_blank">
                            Github
                         </a>
                    </div>
                    <div className="footer-link-items">
                        <h2>Links</h2>
                        <Link to='./Home'>Home</Link>
                        <Link to='./About'>About</Link>
                        <Link to='./Portfolio'>Portfolio</Link>
                        <Link to='./Resume'>Resume</Link>
                    </div>
                </div>
            </div>
            <section className="social-media">
                <div className="social-media-wrap">
                    <div className="footer-logo">
                        <Link to='/' className="social-logo" >
                            <BsFileEarmarkCode className='social-icon' />
                        TONY
                        </Link>
                    </div>
                    <small className="website-rights">Copyright © Anthony Okwananke. All rights reserved.</small>
                    <div className="social-icons">
                        <Link className="social-icon-link LinkedIn"
                            to='/'
                            target='_blank'
                            aria-label='LinkedIn'
                        >
                            <i className='fab fa-linkedin' />
                        </Link>
                        <Link className="social-icon-link GitHub"
                            to='/'
                            target='_blank'
                            aria-label='GitHub'
                        >
                            <i className='fab fa-github' />
                        </Link>
                        <Link className="social-icon-link Instagram"
                            to='/'
                            target='_blank'
                            aria-label='Instagram'
                        >
                            <i className='fab fa-instagram' />
                        </Link>
                    </div>
                </div>

            </section>

        </div>
    )
}

export default Footer
